import { inject, reactive, toRefs } from '@vue/composition-api';
export function useState() {
  const theme = inject('theme', {});
  const settings: any = inject<unknown>('settings', {});
  const sections: any = inject<unknown>('sections', {});

  const card: any = inject<unknown>('card', {});

  const data = reactive({
    info: settings.info,
    isDark: !settings?.colores?.light,
  });

  const media = reactive({
    imgLogo: data.isDark ? data.info['logo-dark'] : data.info['logo-dark'],
  });

  return {
    theme,
    card,
    sections,
    ...data,
    ...media,
  };
}

export function useSections() {
  const sections: { nosotros: any } = inject<any>('sections', {});

  return sections;
}
