

































import { defineComponent, ref } from '@vue/composition-api';
export default defineComponent({
  props: {
    data: {
      type: Object,
      required: true,
    },
  },
  setup() {
    const share = (data: any) => {
      if (navigator.share) {
        navigator.share({
          title: data.name,
          text: data.name,
          url: `https://youtu.be/${data.link}`,
        });
      }
    };
    return {
      share,
    };
  },
});
