


















































import Vue from 'vue';

import { defineComponent, ref, computed, inject } from '@vue/composition-api';
import VueMapbox, { MglMap, MglMarker } from 'vue-mapbox';
import Mapbox from 'mapbox-gl';
Vue.use(VueMapbox, { mapboxgl: Mapbox });

export default defineComponent({
  components: {
    MglMap,
    MglMarker,
  },
  props: {
    data: {
      type: Object,
      required: true,
    },
    ubication: {
      type: Object,
    },
    accessToken: {
      type: String,
      required: true,
    },
  },
  setup(props) {
    const sections = inject<any>('sections', {});

    const urlGo = computed(() => {
      const [lat, log] = props?.ubication?.coordinates || [];
      return `https://www.google.com/maps/search/?api=1&query=${log},${lat}&query_place_id=ChIJye2HTcLn8ZMRDh3GD-MAsG0`;
      // return isSafari
      //   ? `http://maps.google.com/maps?ll=${lat},${log}`
      //   : `geo:0,0?q=${log},${lat}`;
    });

    // const { coords, locatedAt, error } = useGeolocation({});

    const onMapLoaded = async (event: any) => {
      const asyncActions = event.component.actions;

      await asyncActions.flyTo({
        center: props?.ubication?.coordinates,
        zoom: 15,
        speed: 2,
      });
    };
    return {
      onMapLoaded,
      urlGo,
      sections,
      mapStyle: 'mapbox://styles/mapbox/streets-v11',
    };
  },
});
