import { PluginObject } from 'vue';
import { ManifestTheme } from '@/types';
interface Options {
  themes: Record<string, ManifestTheme>;
  theme: string;
}
const plugin: PluginObject<Options> = {
  install(vue, options) {
    const theme = options?.themes[options.theme];
    const registerComponents = (components: any) => {
      for (const key in components) {
        vue.component(key, (components as any)[key]);
      }
    };
    if (theme) {
      registerComponents(theme.components);
    } else {
      const basic = options?.themes['basic'];
      registerComponents(basic?.components);
    }
  },
};

export default plugin;
