


















































import { defineComponent, ref } from '@vue/composition-api';
import VideosDescription from './VideosDescription.vue';

export default defineComponent({
  components: { VideosDescription },
  props: {
    data: {
      type: Object,
      required: true,
    },
  },

  setup(props) {
    const onboarding = ref(0);
    // const data = computed(() => sections.videos?.videos || []);
    const totalVideos = props.data.videos.length;
    const prev = () => {
      onboarding.value =
        onboarding.value + 1 === totalVideos ? 0 : onboarding.value + 1;
    };

    const next = () => {
      onboarding.value =
        onboarding.value - 1 < 0 ? totalVideos - 1 : onboarding.value - 1;
    };

    return {
      onboarding,
      prev,
      next,
    };
  },
});
