








































import { defineComponent, computed } from '@vue/composition-api';
import { useState } from '@/use';
import CardAvatar from './CardAvatar.vue';
import CardNames from './CardNames.vue';
import CardActions from './CardActions.vue';
import CardRedes from './CardRedes.vue';
export default defineComponent({
  components: { CardAvatar, CardNames, CardActions, CardRedes },
  props: {
    data: {
      type: Object,
      required: true,
    },
  },
  setup(props) {
    const { info } = useState();
    const bg = computed(() => {
      return info?.['background-card'];
    });
    const share = () => {
      const text = `${props.data.firstname} ${props.data.surname}`;
      if (navigator.share) {
        navigator.share({
          text,
          title: text,
          url: `${location.origin}${location.pathname}`,
        });
      }
    };
    return { bg, share };
  },
});
