import Vue from 'vue';
import App from './App.vue';
import vuetify from '@/config/plugins/vuetify';
import VueComposition from '@vue/composition-api';
import PluginThemes from './plugin';
import { sections, themes } from '@/themes';
import router from './routes';
import '@/components/index';
import '@/config/styles/main.css';
import './app.scss';
import 'roboto-fontface/css/roboto/roboto-fontface.css';
import './registerServiceWorker';

Vue.use(VueComposition);
Vue.use(PluginThemes, { themes, theme: 'basic' });
Vue.config.productionTip = false;
/**
 * Load data
 */
const _data = document.getElementById('data')?.textContent;
let data: Record<string, any> = {};
data = JSON.parse(_data!);
data.app = { sections };
/**
 * Vue init
 */
new Vue({
  router,
  vuetify: vuetify(data?.settings?.colores),
  provide: data,
  data: {
    data,
  },
  render: (h) => h(App),
}).$mount('#app');
